import React, {Component} from 'react';
import './App.css';
import HomePage from './Layouts/HomePage';
import CreateConfigurePage from './Layouts/CreateConfigurePage';
import SelectModePage from './Layouts/SelectModePage';
import CssBaseline from '@material-ui/core/CssBaseline'
import { ThemeProvider } from '@material-ui/styles'
import {createMuiTheme} from '@material-ui/core/styles'
import { HashRouter, Route } from "react-router-dom";
import BrowserDetection from 'react-browser-detection';

const theme = createMuiTheme({
  palette:{
    primary:{
      main:"#009FE5"
    },
    secondary:{
      main:"#009FE5"
    }
  },
  overrides: {
    MuiButton: {
      text: {
        fontSize:"0.9rem",
        border:"none",
        borderRadius:25,
        padding:"8px 25px",
        cursor:"pointer",
        backgroundColor:"#009FE5",
        fontWeight:"bold",
        textTransform:"initial",
        color:"white!important"
      }
    },
  },
});

const browserHandler = {
  chrome: () =>{
    return(
      <>
      <CssBaseline/>
      <ThemeProvider theme={theme}>
      <HashRouter basename='/'>
        <Route exact path="/" component={() => <HomePage/>} />
        <Route path="/create/:referredCourseId?" component={(props) => <CreateConfigurePage {...props} editMode="create"/>}/>
        <Route path="/select-mode/" component={(props) => <SelectModePage {...props}/>}/>
        <Route path="/configure/:referredCourseId?" component={(props) => <CreateConfigurePage {...props}  editMode="configure"/>}/>
      </HashRouter>
      </ThemeProvider>
      </>
    )
  },
  default: (browser) => <div style={{height:"100vh", display:"flex",alignItems:"center",justifyContent:"center",fontSize:"1.5rem"}}>Please open this interface on Chrome</div>,
};




function App(){
    return (
      <div>
          <BrowserDetection>
            { browserHandler }
          </BrowserDetection>
      </div>
    );


}

export default App;
